<template>
  <div>
    <div
      :id="frameState.startFrame"
      class="framerep"
      v-click-outside="() => contextmenu = -1"
    >
      <div :class="frameState.startFrame.slice(0, 15) === 'undefined-frame' || !frameIds.find(a => a === frameState.startFrame) ? 'is--error' : ''">
        <div v-if="frameState.startFrame.slice(0, 15) !== 'undefined-frame'" class="framerep-action is--id">
          <img
            src="../images/font-2.svg"
            loading="lazy"
            alt=""
            class="icon-img is--xlarge"
          />
          <div class="little-row2">
            <span style="margin: 0.3rem 0.3rem 0 0;">#</span>
            <select
              class="dropdown is--template-border"
              style="max-width: 20rem;"
              v-model="frameModel"
            >
              <option
                class="dropdown-list w-dropdown-list w--open"
                disabled
                value=""
              >
                # Frame ID
              </option>
              <option
                class="dropdown-list w-dropdown-list w--open"
                v-for="(option, i) in frameIds"
                :value="option"
                :key="i"
              >
                <ul>
                  <li>
                    <label>{{ option }}</label>
                  </li>
                </ul>
              </option>
            </select>
          </div>
        </div>

        <div v-else class="framerep-action is--id">
          <div>{{ frameState.startFrame }}</div>
          <a
            class="state_actionbtn is--clear large-blur"
            @click="$emit('on-delete-undefined-frame', frameState.startFrame)"
          >
            <i class="fa fa-trash" style="color: #FF485A;"></i>
          </a>
        </div>

        <div
          v-if="frameState && frameState.transformation && frameState.transformation[0]"
        >
          <div
            v-for="(compItem, index) in frameState.transformation"
            :key="index"
            @contextmenu="(e) => onRightClick(e, index)"
          >
            <div
              @click="$emit('on-select-transformation', $event, frameState.startFrame, index)"
              class="state-w"
              :class="[
                changingTargetComponent &&
                changingTargetComponent.frameId == frameState.startFrame &&
                changingTargetComponent.componentElement == index
                  ? 'is--changing-target'
                  : !compItem.compId || (compItem.attribute.slice(0, 4) !== 'SYNC' && !compItem.value) || !compItem.attribute ? 'is--error-colored' : '',
                compItem.compId && compItem.compId === selectedExpressionId ? 'is--expactive' : '',
                selectingComps.find(a => a.frameId === frameState.startFrame && a.componentElement === index)
                  ? 'is--select'
                  : '',
              ]"
            >
              <img v-if="setAutoImage(compItem) === 'slide'" src="../images/slide.svg" loading="lazy" alt="" class="slider"/>
              <img v-else-if="setAutoImage(compItem) === 'axis-3'" src="../images/axis-3.svg" loading="lazy" alt=""/>
              <img v-else-if="setAutoImage(compItem) === 'function-2'" src="../images/function-2.svg" loading="lazy" alt=""/>
              <img v-else-if="setAutoImage(compItem) === 'button'" src="../images/button.svg" loading="lazy" alt=""/>
              <div class="comp_id-w">{{ compItem.desmosId }}-{{ compItem.expidx }}</div>
              <div class="">{{
                compItem.latex
                  ? compItem.latex.replaceAll(String.fromCharCode(92), "").replaceAll("left", "").replace("right", "").slice(0, 10)
                  : ''
              }}</div>
              <h4>{{ compItem.attribute ? compItem.attribute.slice(0, 11): '' }} {{ compItem.value ? ' : ' + compItem.value : ''}}</h4>
            </div>
          </div>
        </div>

        <div
          v-else
          class="is--center frame-text"
          @contextmenu="(e) => onRightClick(e, -2)"
        >
          No Transformations
        </div>
        <div
          v-if="contextmenu > -1"
          class="right-click"
          :style="contextmenuPosition.x  < 200 ? `left: ${contextmenuPosition.x}px; top: ${contextmenuPosition.y -120 }px;`: `left: 200px; top: ${contextmenuPosition.y -120 }px;`"
        >
          <div @click="$emit('on-copy-transformation', frameState.startFrame, contextmenu), contextmenu = -1" class="dropdown_item">copy <span style="margin-right: 0px;">Ctrl+c</span></div>
          <div @click="$emit('on-change-target', frameState.startFrame, contextmenu), contextmenu = -1" class="dropdown_item">change target<span style="margin-right: 0px;">Ctrl+q</span></div>
          <div v-if="copyingComps[0]" @click="$emit('on-paste-transformation', frameState.startFrame, contextmenu), contextmenu = -1" class="dropdown_item">paste<span style="margin-right: 0px;">Ctrl+v</span></div>
          <div @click="$emit('on-duplicate-transformation', frameState.startFrame, contextmenu), contextmenu = -1" class="dropdown_item">duplicate<span style="margin-right: 0px;">Ctrl+d</span></div>
          <div @click="$emit('on-delete-transformation', frameState.startFrame, contextmenu), contextmenu = -1" class="dropdown_item">delete<span style="margin-right: 0px;">Backspace</span></div>
        </div>
        <div
          v-if="contextmenu === -2 && copyingComps[0]"
          class="right-click"
          :style="contextmenuPosition.x  < 200 ?`
            left: ${contextmenuPosition.x}px;
          `: 'left: 200px'"
        >
          <div @click="$emit('on-paste-transformation', frameState.startFrame, 0), contextmenu = -1" class="dropdown_item">paste<span style="margin-right: 0px;">Ctrl+v</span></div>
        </div>

        <div
          class="framerep-action"
          @click="onAddTransformation(frameState.startFrame)"
        >
          <img
            src="../images/add.svg"
            loading="lazy"
            alt=""
            class="icon-img is--xlarge"
          />
          <div>Add Transformation</div>
        </div>
      </div>


    </div>

  </div>
</template>

<script>
export default {
  name: "FrameComp",
  props: {
    frameIds: {
      type: Array,
    },
    frameState: {
      type: Object,
    },
    changingTargetComponent: {
      type: Object,
    },
    selectingComps: {
      type: Array,
    },
    copyingComps: {
      type: Array,
    },
    selectedExpressionId: {
      type: String,
    }
  },
  components: {
  },
  data: () => ({
    contextmenu: -1,
    contextmenuPosition: {
      x: 0,
      y: 0,
    },
  }),
  computed: {
    frameModel: {
      get () { return this.frameState.startFrame },
      set (value) { this.$emit('on-change-frameId', this.frameModel, value) },
    },
  },
  methods: {
    onAddTransformation(frameId) {
      this.$emit("on-add-transformation", frameId);
    },

    // right click and directives
    onRightClick(e, index) {
      e.preventDefault();
      this.contextmenu = index;
      this.contextmenuPosition = {
        x: e.clientX,
        y: e.pageY,
      };
    },

    setAutoImage: function (compItem) {
      let icon = 'function-2';
      // detect desmos canvas
      if (compItem.compId === "desmosCanvas") {
        icon = 'axis-3'
      }
      const ilatex = compItem?.latex?.split("=")?.pop();
      if (!isNaN(ilatex)) {
        icon = 'slide'
      }
      const lLatex = compItem?.latex?.slice(1, 6);
      if (lLatex === "left(") {
        icon = 'button'
      }
      return icon;
    }
  },

  directives: {
    "click-outside": {
      bind: function (el, binding, vNode) {
        // Provided expression must evaluate to a function.
        if (typeof binding.value !== "function") {
          const compName = vNode.context.name;
          let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`;
          if (compName) {
            warn += `Found in component '${compName}'`;
          }

          console.warn(warn);
        }
        // Define Handler and cache it on the element
        const bubble = binding.modifiers.bubble;
        const handler = (e) => {
          if (bubble || (!el.contains(e.target) && el !== e.target)) {
            binding.value(e);
          }
        };
        el.__vueClickOutside__ = handler;

        // add Event Listeners
        document.addEventListener("click", handler);
      },

      unbind: (el) => {
        // Remove Event Listeners
        document.removeEventListener("click", el.__vueClickOutside__);
        el.__vueClickOutside__=null;
      },
    },
  },
};
</script>

